<template>
  <div class="rtc container">
    <div class="webrtc">
      <a ref="link">WebRTC <i ref="icon"></i></a>
    </div>
    <div class="blank"></div>
  </div>
</template>

<script>
export default {
  name: 'WebRTC',
  mounted() {
    if(Hls.WEBRTC_SUPPORT) {
      this.$refs.icon.className = 'fa fa-check'
      this.$refs.link.style.backgroundColor = 'green'
    } else {
      this.$refs.icon.className = 'fa fa-close'
      this.$refs.link.style.backgroundColor = 'red'
    }
  }
}
</script>

